.search-tag {
    position: relative;

    margin-bottom: 1rem;
}
.search-tag legend {
    font-size: 1rem;
}
.search-tag input {
    float: left;
}

.search-tag input[type="text"] {
    font-size: .7rem;

    width: 100%;
    height: 2rem;
    padding-left: 1rem;

    border: 1px solid #999999;
    border-radius: 1rem;
    outline: none;
}
.search-tag input[type="text"]:focus {
    border: 2px solid #5cb85c;
}

.search-tag input[type="submit"] {
    font-size: .7rem;

    position: absolute;
    top: 50%;
    right: .5rem;

    width: 1.7rem;
    height: 1.7rem;
    margin-top: .1rem;

    border: 1px solid #ffffff;
    border-radius: 1rem;
    outline: none;
    background: none;
}

.search-tag input[type="submit"]:active {
    background-color: #999999;
}
.search-tag input[type="submit"]:hover {
    background-color: #999999;
}
.search-tag input[type="submit"]:focus {
    background-color: #5cb85c;
}



