.menu-button {
    position: absolute;
    left: 100%;

    margin: 1rem;
    margin-top: 0.5rem;
    margin-left: 2rem;
    padding: 10px;

    cursor: pointer;
    -webkit-transition: .3s;
            transition: .3s;

    border-radius: 5px;
    background-color: #5cb85c;
}
.menu-button:hover {
    border-radius: 1px;
    background-color: #2c8dfb;
}
.menu-button:active {
    border-radius: 1px;
    background-color: #2c8dfb;
}
.menu-button .bar:nth-of-type(1) {
    margin-top: 0;
}
.menu-button .bar:nth-of-type(3) {
    margin-bottom: 0;
}

.bar {
    position: relative;

    display: block;

    width: 30px;
    height: 3px;
    margin: 6px auto;

    -webkit-transition: .3s;
            transition: .3s;

    border-radius: 10px;
    background-color: #ffffff;
}

.slideShelter{
    position: absolute;
    top:0;
    right:0;
    z-index: 2;
    height: 100%;
    width: 100%;
    background-color: rgba(2, 20, 41, 0.5);
    color: #fff;
}
.shelterHide{
  display: none;
}


